html,
body,
#root {
  width: 100%;
  height: 100%;
}

.loading {
  width: 50px;
  height: 40px;
  margin: 40px auto;
}

.highlight {
  transform: scale(1.2);
}

.fullWidth {
  width: 100% !important;
}

@media (max-width: 768px) {
  body {
    height: 100vh;
  }
}

::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  opacity: 0.6;
}
::-moz-placeholder {
  /* Firefox 19+ */
  opacity: 0.6;
}
:-ms-input-placeholder {
  /* IE 10+ */
  opacity: 0.6;
}
:-moz-placeholder {
  /* Firefox 18- */
  opacity: 0.6;
}

#loader {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99999;
}

#loader .spinner {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
#loader .spinner div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #fff;
  border-radius: 50%;
  animation: spinner 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #fff transparent transparent transparent;
}
#loader .spinner div:nth-child(1) {
  animation-delay: -0.45s;
}
#loader .spinner div:nth-child(2) {
  animation-delay: -0.3s;
}
#loader .spinner div:nth-child(3) {
  animation-delay: -0.15s;
}
@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
